import React, { Component } from 'react';

const Plotly = window.Plotly

class Plot extends React.Component {

  componentDidMount(){
    var trace1 = {
      x: [1, 2, 3, 4],
      y: [10, 15, 13, 17],
      type: 'scatter'
    };

    var trace2 = {
      x: [1, 2, 3, 4],
      y: [16, 5, 11, 9],
      type: 'scatter'
    };

    var data = [trace1, trace2];

    Plotly.newPlot('plot', data);
  }
  render () {
    return (
      <div id="plot"></div>
    )
  }
}

export default Plot;



